let counter = 0

export const counterClicked = () => {
    return {
        type: 'INCREMENT_COUNTER',
        counter: ++counter
    }
}

export const choiceSelected = (choiceText) => {
    return {
        type: 'CHOICE_SELECTED',
        choiceText: choiceText
    }
}


