import React, {PropTypes} from 'react'
import ChoiceText from './choiceText'

const choices = ({q, onChoiceSelected}) => (

    <div>
        <ChoiceText value={ q.choice1 } onClick={ onChoiceSelected }> </ChoiceText>
        <ChoiceText value={ q.choice2 } onClick={ onChoiceSelected }> </ChoiceText>
        <ChoiceText value={ q.choice3 } onClick={ onChoiceSelected }> </ChoiceText>
        <ChoiceText value={ q.choice4 } onClick={ onChoiceSelected }> </ChoiceText>
    </div>
)

choices.propTypes = {
  q: PropTypes.object.isRequired,
  onChoiceSelected: PropTypes.func.isRequired
}

export default choices



