import React, {PropTypes} from 'react'

const incrementer = ({onClicked}) => (
    <div>
        <button type="button" onClick={e => {
         e.preventDefault()
         onClicked()
       }}>
       Increment!
       </button>
    </div>
)

incrementer.propTypes = {
  onClicked: PropTypes.func.isRequired
}

export default incrementer



