const generateQuestion = () => {

    var randomIndices = [];

    while(randomIndices.length < 4){
        var randomIndex;
        var added = false;
        while (!added) {
            randomIndex = Math.floor(Math.random() * words.length);
            var found = false;
            for (var i = 0; i < randomIndices.length; i++) {
                if (randomIndices[i] == randomIndex){
                    found = true;
                    break;
                }
            }

            if (!found) {
                randomIndices.push(randomIndex);
                added = true;
            }
        }
    }

    var question = words[randomIndices[Math.floor(Math.random() * randomIndices.length)]];

    var choice1 = words[randomIndices[0]];
    var choice2 = words[randomIndices[1]];
    var choice3 = words[randomIndices[2]];
    var choice4 = words[randomIndices[3]];

    /*var choices = [question.meaning, choice1.meaning, choice2.meaning, choice3.meaning];

    for (i = 0; i < 10; i++){
        var randomElemIndex = Math.floor(Math.random * choices.length);
        var randomElem = choices[randomElemIndex];

        choices.slice(randomElemIndex, 1);

        choices.push(randomElem);
    }
*/
    return {
        text: question.word,
        answer: question.meaning,
        choice1: choice1.meaning,
        choice2: choice2.meaning,
        choice3: choice3.meaning,
        choice4: choice4.meaning
    }
};

const counter = (state = 0, action) => {
    switch (action.type) {
        case 'INCREMENT_COUNTER':
            return action.counter
        default:
            return state
    }
}

const question = (state = generateQuestion(), action) => {
    console.log(state);
    switch (action.type) {
        case 'CHOICE_SELECTED':
            if (state.answer == action.choiceText) {
                return generateQuestion();
            }
            else {
                alert('yanlış')
                return state
            }
        default:
            return state
    }
}


const counterApp = combineReducers({
  counter,
  question
})

export default counterApp


