import React, {PropTypes} from 'react'
import QuestionText from './questionText'
import Choices from './choices'

const question = ({q, onChoiceSelected}) => (

    <div>
        <QuestionText key='1' value={ q.text }> </QuestionText>
        <Choices q={q} onChoiceSelected={onChoiceSelected} />
    </div>
)

question.propTypes = {
  q: PropTypes.object.isRequired,
  onChoiceSelected: PropTypes.func.isRequired
}

export default question



