
import { connect } from 'react-redux'
import Incrementer from '../components/incrementer'
import { counterClicked } from '../actions'


const mapDispatchToProps = (dispatch) => {
  return {
    onClicked: () => {
      dispatch(counterClicked())
    }
  }
}

 const IncrementerComponent = connect(
     null,
     mapDispatchToProps
)(Incrementer)

export default IncrementerComponent


